<template>
  <div class="animated fadeIn">
    <b-row>
      <b-col>
        <b-card>
          <div slot="header">
            <span>
              <i class="fa fa-book"></i>
              Products ({{productCount}})
              <b-link class="pull-right" @click="cwCheckPrice">CW Check Price</b-link>
            </span>
          </div>

          <b-table
            ref="trackingProduct"
            responsive="sm"
            :items="products"
            :fields="fields"
            :small="true"
            class="tracking"
            selectable
            select-mode="single"
            @row-clicked="productClicked"
            @row-dblclicked="productDoubleClicked"
          >
            <template v-slot:cell(index)="data">{{data.index + 1}}</template>
            <template v-slot:cell(name)="data">
              <div class="prod-thumbnail-name">
                <div class="thumbnail">
                  <img
                    :src="data.item.thumbnail"
                    width="50px"
                    height="50px"
                    v-if="data.item.thumbnail"
                  />
                </div>
                <div class="name-2">
                  {{data.value}}
                  <br />
                  <small>PID: {{data.item.cwProductId}}</small>
                </div>
              </div>
            </template>
          </b-table>
        </b-card>
      </b-col>
    </b-row>
    <!--/.row-->
  </div>
</template>

<script>
import fb from "firebase";
import productDetail from "./productDetail";
import { generateSubString } from "@/shared/utils";

export default {
  name: "Products",
  components: {
    productDetail
  },
  data() {
    return {
      products: [],
      product: {},
      productBatches: "",
      useMobileView: false,
      searchText: "",
      pageSize: 10,
      lastVisible: undefined,
      productCount: 0,
      isLoadingMore: false,
      fields: [
        { key: "index", label: "#", class: "index" },
        { key: "name", class: "name", class: "name" },
        { key: "price", label: "Price", class: "price" },
        { key: "lastChecked", label: "Last Checked", class: "last-checked" },
        { key: "action", label: "Action", class: "action" }
      ]
    };
  },
  mounted: function() {
    var self = this;
    self.useMobileView = window.innerWidth < 700;
    window.addEventListener("resize", function() {
      self.useMobileView = window.innerWidth < 700;
    });
    this.loadProducts().then(products => this.checkCwPrice(products));
    this.$fb.counterService.getProductCount().then(count => (this.productCount = count));
  },
  watch: {
    searchText: function(newValue, oldValue) {
      if (oldValue != "" && newValue == "") {
        this.products = [];
        this.loadProducts();
      }
    }
  },
  computed: {},
  methods: {
    loadProducts: function(lastVisible) {
      return this.$fb.productCollection
        .where("cwTracking.percentage", ">", "0")
        .orderBy("cwTracking.percentage")
        .startAfter(this.lastVisible || 0)
        .limit(10)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            this.lastVisible = _.last(snapshot.docs);
            snapshot.forEach(doc => {
              this.products.push(doc.data());
            });
            return this.products;
          }
        });
    },
    loadMoreProduct: function(e) {
      this.isLoadingMore = true;
      if (this.searchText) {
        this.searchProduct(this.lastVisible).then(() => (this.isLoadingMore = false));
      } else {
        this.loadProducts(this.lastVisible).then(() => (this.isLoadingMore = false));
      }
    },
    productClicked: function(product) {
      this.product = { ...product };
    },

    checkCwPrice: function(products) {},
    productClicked: function() {},
    productDoubleClicked: function() {},
    cwCheckPrice: function() {
      this.$fb.productCollection
        .where("cwProductId", ">", "0")
        .orderBy("cwProductId")
        .limit(10)
        .get()
        .then(snapshot => {
          if (!snapshot.empty) {
            return _.map(snapshot.docs, doc => doc.data());
          } else return [];
        })
        .then(products => {});
    }
  }
};
</script>
<style>
.tracking th.index {
  width: 1%;
}

.tracking th.price {
  width: 15%;
}

.tracking th.last-checked {
  width: 10%;
}

.tracking th.action {
  width: 5%;
}

.prod-thumbnail-name {
  display: flex;
  justify-content: flex-start;
}
.prod-thumbnail-name .name-2 {
  margin-left: 5px;
}
.prod-thumbnail-name .thumbnail {
  width: 50px;
  min-width: 50px;
  height: 50px;
  min-height: 50px;
}
</style>
